.graphic-message-list-wrapper {
  .message-list-card {
    max-height: 320px;
    min-height: 91px;
    overflow: auto;
    &.ant-card-bordered {
      .ant-list-item {
        padding: 5px 0;
        text-align: center;
        margin: 0;
      }
    }
    .ant-card-head, .ant-card-body, .ant-card-head-title {
      padding: 0;
      white-space: normal;
      border-bottom: 0;
    }
    .message-list {
      position: relative;
      &::-webkit-scrollbar {
        display: none;
      }
      .ant-list-item {
        padding: 0;
        margin-bottom: 10px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
      .head-message {
        position: relative;
        width: 100%;
        height: 120px;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
        p {
          width: 100%;
          min-height: 20px;
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 2;
          background: rgba(0, 0, 0, .7);
          color: #fff;
          padding: 5px 10px;
          text-align: left;
        }
      }
      .rest-message {
        width: 100%;
        height: 60px;
        padding: 5px 10px;
        p {
          width: 79%;
          float: left;
          text-align: left;
          color: #949494;

        }
        img {
          width: 40px;
          height: 40px;
          float: right;
          //vertical-align: middle;
        }
      }
    }
    .border_btm {
      border-bottom: 1px solid #eee;
    }
    .head-other {
      font-size: 12px;
      padding: 15px 10px;
      box-sizing: border-box;
      img {
        float: right;
        width: 50px;
        height: 50px;
        border-radius: 3px;
        margin-top: 5px;
        margin-left: 5px;
      }
      .title {
        margin-bottom: 5px;
        font-weight: bolder;
      }
      p {
        color: #eee;
      }
    }
  }
}

.ellipse-two {
  -webkit-line-clamp: 2;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
}

