.message-group-wrapper {
  .message-sider-wrapper {
    //margin-right: 20px;
    border-right: 1px solid #eee;
    min-height: 440px;
    padding-right: 20px;
    overflow: auto;
    .search-input {
      width: 160px;
      margin-bottom: 10px;
    }
    .ant-collapse-item {
      border-bottom: 0;
      .ant-collapse-header {
        padding-left: 25px;
      }
      i.ant-collapse-arrow {
        left: 0;
      }
      .ant-collapse-content > .ant-collapse-content-box {
        padding: 0;
      }
      .gonghao-list {
        width: 160px;
        .ant-list-item {
          padding: 8px 10px;
          span {
            margin-right: 10px;
          }
          &.selected {
            background: #00a0fe;
            color: #fff;
          }
        }
        .ant-list-empty-text {
          display: none;
        }
      }
    }
    .ant-list-item {
      border-bottom: 0;
      padding: 0;
    }
    .ant-collapse-item:last-child > .ant-collapse-content {
      border-radius: 0;
    }
  }
  .ant-layout-content {
    > div {
      position: relative;
    }
  }
}