.edit-message {
  overflow: hidden;
  padding: 0;
  .show-material-wrapper {
    width: 300px;
    max-height: 300px;
    border: 1px solid #ddd;
    overflow: auto;
    border-radius: 10px;
    margin-right: 200px;
    ul {
      li {
        width: 100%;
        box-sizing: border-box;
        padding: 14px 14px 0;
        line-height: 50px;
        cursor: pointer;

        .material-item {
          border-bottom: 1px solid #ddd;
          padding-bottom: 14px;
          overflow: hidden;

          .material-title {
            width: 180px;
            color: #353535;
            font-size: 14px;
          }

          .material-img {
            background: #ddd;
            margin-left: 15px;
            width: 50px;
            height: 50px;
            overflow: hidden;

            img {
              width: 100%;
              height: 100%;
            }
          }
        }

        &.add-material {
          font-size: 30px;
          padding-bottom: 14px;
        }
      }
    }
  }
  .kefu-form {
    width: 520px;
    box-sizing: border-box;
    margin-left: 50px;
    .ant-form-item-label {
      text-align: left;
    }
    .text-message {
      .ant-form-item-label {
        line-height: inherit;
      }
    }
    .block-title {
      font-size: 24px;
      margin-left: -10px;
      margin-bottom: 20px;
    }
    .gonghao-tag {
      font-size: 16px;
      line-height: 1.5;
      i {
        font-size: 16px;
      }
    }
  }
}
.params-modal {
  width: 1200px !important;
  .ant-modal-body {
    height: 360px;
    .gonghao-transfer {
      width: 410px;
      margin-right: 20px;
      display: inline-block;
      text-align: center;
      .ant-transfer-list {
        height: 300px;
        text-align: left;
      }
    }
    .params-form {
      width: 720px;
    }
  }

}

.tips {
  font-style: italic;
  color: red;
  margin-top: 5px;
  font-size: 14px;
  display: inline-block;
  span {
    font-size: 16px;
    color: blue;
  }
}

