.reply-form {
  width: 1000px;
  box-sizing: border-box;
  margin-left: 50px;
  .text-message {
    .ant-form-item-label {
      line-height: inherit;
    }
  }
  .block-title {
    font-size: 24px;
    margin-left: -10px;
    margin-bottom: 20px;
  }
  .message-block {
    width: 100%;
    min-height: 100px;
    border: 1px solid #eee;
    border-radius: 5px;
    padding: 10px 20px;
    box-sizing: border-box;
    .ant-form-item {
      margin-bottom: 0;
    }
  }
  .template-wrapper {
    margin-bottom: 24px;
  }
  .inputtxt {
    .ant-form-item-label {
      line-height: inherit;
    }
  }
  .tips {
    font-size: 12px;
    font-style: italic;
    color: red;
    display: block;
  }
}