.message-list-wrapper {
  .message-list-card {
    &.ant-card-bordered {
      .ant-list-item {
        padding: 5px 0;
        text-align: center;
        margin: 0;
      }
    }
    .ant-card-head, .ant-card-body, .ant-card-head-title {
      padding: 0;
      white-space: normal;
      border-bottom: 0;
    }
    .message-list {
      height: 436px;
      overflow: auto;
      position: relative;
      &::-webkit-scrollbar {
        display: none;
      }
      .update-time {
        height: 30px;
        line-height: 30px;
        padding-left: 10px;
        i {
          padding-right: 5px;
        }
      }
      .ant-list-item {
        padding: 0;
        margin-bottom: 10px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
      .head-message {
        position: relative;
        width: 100%;
        height: 120px;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
        p {
          width: 100%;
          min-height: 20px;
          position: absolute;
          bottom: 0;
          left: 0;
          z-index: 2;
          background: rgba(0, 0, 0, .7);
          color: #fff;
          padding: 5px 10px;
          text-align: left;
        }
      }
      .rest-message {
        width: 100%;
        height: 60px;
        padding: 5px 10px;
        p {
          width: 75%;
          float: left;
          text-align: left;
          color: #949494;

        }
        img {
          width: 40px;
          height: 40px;
          float: right;
          //vertical-align: middle;
        }
      }

    }
    .action-wrapper {
      overflow: hidden;
      .ant-col,.ant-col-6 {
        border-right: 1px solid #eee;
        &:nth-of-type(3) {
          border-right: 0;
        }
      }
    }
    .border_btm {
      border-bottom: 1px solid #eee;
    }
  }
}

.ellipse-two {
  -webkit-line-clamp: 2;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
}

