.basic-layout {
  .basic-sider {
    min-height: calc(100vh - 50px);
  }
  .basic-content {
    //margin-left: 2;
    .basic-card {
      min-height: calc(100vh - 90px);
      .basic-header {
        background: #fff;
        padding: 0;
        border-bottom: 2px solid #eee;
        height: 43px;
      }
      .connect {
        position: fixed;
        right: -5px;
        top: 50%;
        margin-top: -40px;
        width: 30px;
        height: 80px;
        background: #1890ff;
        border-radius: 10px;
        z-index: 1;
        cursor: pointer;
        text-align: center;
        padding: 8px 5px 0 0;
        color: #fff;
        font-size: 14px;
        .connect-method {
          width: 200px;
          height: 210px;
          position: absolute;
          left: -210px;
          bottom: -80px;
          padding-top: 20px;
          transition: all .3s;
          display: none;
          background: #f0f0f0;
          .connect-qrcode {
            width: 150px;
            height: 150px;
          }
          .connect-title {
            color: #000;
            line-height: 30px;
          }
        }
        &:hover .connect-method {
          display: block;
          transition: all .3s;
          animation: show 0.3s;
          -moz-animation: show  0.3s; /* Firefox */
          -webkit-animation: show  0.3s; /* Safari and Chrome */
          -o-animation: show  0.3s; /* Opera */
        }
      }
    }
  }
  .basic-footer {
    text-align: center;
    padding: 10px 0;
  }
  .ant-card-body {
    padding: 0;
  }
}

@keyframes show{
  0%{transform: translate(300px, 0) }
  100%{transform: translate(0, 0) }
}