.edit-message {
  overflow: hidden;
  .show-material-wrapper {
    width: 300px;
    max-height: 300px;
    border: 1px solid #ddd;
    overflow: auto;
    border-radius: 10px;
    margin-right: 200px;
  }
  .kefu-form {
    width: 520px;
    box-sizing: border-box;
    margin-left: 50px;
    .ant-form-item-label {
      text-align: left;
    }
    .text-message {
      .ant-form-item-label {
        line-height: inherit;
      }
    }
    .block-title {
      font-size: 24px;
      margin-left: -10px;
      margin-bottom: 20px;
    }
    .message-block {
      width: 100%;
      min-height: 100px;
      border: 1px solid #eee;
      border-radius: 5px;
      padding: 10px 20px;
      box-sizing: border-box;
      .ant-form-item {
        margin-bottom: 0;
      }
    }
    .template-wrapper {
      margin-bottom: 24px;
    }
  }

}
.params-modal {
  width: 1200px !important;
  .ant-modal-body {
    height: 360px;
    .gonghao-transfer {
      width: 410px;
      margin-right: 20px;
      display: inline-block;
      text-align: center;
      .ant-transfer-list {
        height: 300px;
        text-align: left;
      }
    }
    .params-form {
      width: 720px;
    }
  }

}

