.bread-wrapper {
  margin-bottom:20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: #fff;
  * {
    user-select: none;
  }
  .bread-nav {
    width: 50%;
    padding: 10px 20px;
    box-sizing: border-box;
    color: #0b0b0b;
    border-radius: 8px;
    .ant-breadcrumb span {
      color: rgb(51, 51, 51);
      font-size: 12px;
    }
  }
  .notice {
    width: 500px;
    float: right;
    display: flex;
    flex-direction: row;
    // justify-content: space-around;
    .notice-title {
      color: red;
      width: 90px;
    }
    .notice-desc {
      width: 400px;
      cursor: pointer;
      &:hover {
        color: red;
      }
    }
  }
}