.search-btn {
  width: 200px;
}

.reset-code {
  margin-left: 20px;
  span {
    color: rgb(255, 153, 0);
  }
}

.table-wrapper {
  margin-top: 20px;
  width: 100%;
  .ant-spin-nested-loading {
    min-height: 340px;
  }
  * {
    color: #333;
  }
  .ant-tabs-bar {
    margin: 0;
  }

  .card-grid {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
  }

  .card-item {
    text-align: center;
    .ant-card-body {
      padding: 0;
    }

    li {
      margin: 0;
    }
    .disabled-click {
      color: #eee;
      cursor: not-allowed;
    }
    img {
      width: 50px;
      border-radius: 50%;
      vertical-align: top;
      margin: 20px 0;
    }
    .gonghao-info {
      display: inline-block;
      text-align: left;
      margin: 20px 0;
      margin-left: 5px;
      p {
        padding: 0;
        color: #0d71bb;
        font-weight: 700;
        font-family: "楷体";
        font-size: 18px;
      }
    }
    .active-users-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      border-bottom: 1px solid #e1e1e1;
      font-size: 12px;
      .active-users {
        width: 50%;
        padding: 5px 0;
        &:first-child {
          border-right: 1px solid #e1e1e1;
          box-sizing: border-box;
        }
        span {
          font-weight: bolder;
          font-size: 14px;
          color: #0d71bb;
          display: block;
        }
      }
    }
  }
}
