.multi-gonghao-wrapper {
  .message-sider-wrapper {
    //margin-right: 20px;
    border: 1px solid #eee;
    padding: 10px;
    max-height: 800px;
    padding-right: 0;
    overflow: auto;
    .search-input {
      width: 160px;
      margin-bottom: 10px;
    }
    .ant-list-item {
      border-bottom: 0;
      padding: 0;
    }
    .gonghao-list {
      width: 160px;
      .ant-list-item {
        padding: 8px 10px;
        span {
          margin-right: 10px;
        }
        &.selected {
          background: #00a0fe;
          color: #fff;
        }
      }
      .ant-list-empty-text {
        display: none;
      }
    }
  }
  .ant-layout-content {
    > div {
      position: relative;
    }
  }
}