.table-wrapper {
  width: 100%;
  * {
    color: #333;
  }
  .ant-tabs-bar {
    margin: 0;
  }

  .card-item {
    text-align: center;
    .ant-card-body {
      padding: 0;
    }
    p {
      padding: 20px 0;
    }
    ul {
      border-top: 1px solid #eee;
      li {
        float: left;
        border-right: 1px solid #eee;
        //width: 25%;
        width: 33.3333%;
        box-sizing: border-box;
        padding: 5px 0;
        cursor: pointer;
        user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        -webkit-user-select: none;
        &:last-of-type {
          border: 0;
        }
        &.disabled-click {
          color: #eee;
          cursor: not-allowed;
        }
      }
    }
  }
}
